.button {  
    font-size: 20px;
    color: #222;
    background-color: #FFF;
    border-radius: 4px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    outline: 0;
    border: none;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
}

.button:active {
    -webkit-box-shadow: none;
            box-shadow: none;
}

.blue {
    background-color: rgb(149, 223, 255);
}


